@import url(https://fonts.googleapis.com/css?family=Gelasio:700|Open+Sans:400,600&display=swap);
html,body{max-width:720px;color:#02241F;background-color:#FFFCF0;padding:0px 20px;margin:0px auto;box-sizing:border-box;outline:0;height:100%;font-family:"Open Sans",sans-serif;font-size:16px}h1,h2,h3,h4,h5,h6{font-family:"Gelasio",serif}ul,li{padding:0px;margin:0px}a{text-decoration:none;color:#02241F;-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out}a:hover{color:#FF6060;border-radius:4px;border-bottom:4px solid #FF6060}.container{margin:30px auto 60px}.navbar{display:flex;align-items:baseline;justify-content:space-between;padding:14px 0px;border-bottom:1px solid #FFD853}.navbar_left h1{margin:0px}.navbar_left a{font-size:26px;font-family:"Gelasio",serif}@media (min-width: 576px){.navbar_left a{font-size:36px}}.navbar_left a:hover{border-radius:none;border-bottom:none}.navbar_right{display:flex;align-items:center}.navbar_right a{font-size:12px;margin-left:8px;margin-right:8px;font-weight:600;line-height:0.7;letter-spacing:.1em;opacity:1;text-transform:uppercase}@media (min-width: 576px){.navbar_right a{font-size:14px}}.home div:first-of-type{margin-bottom:20px}@media (min-width: 576px){.home div:first-of-type{margin-bottom:40px}}.home a{color:#FF6060}.home_photos h2 span{color:#559AFF;font-size:12px;font-weight:400}.home_photos img{width:100px;height:100px;border:1px solid #02241F;border-radius:8px;margin-right:5px}.now{padding-bottom:50px}.now_section h1{font-size:24px}.now_section .reads{display:flex;justify-content:space-between;align-items:baseline}.now_section .reads h3{font-size:18px}@media (min-width: 576px){.now_section .reads h3{font-size:20px}}.now_section .reads p{font-size:10px;font-weight:600;text-transform:uppercase}@media (min-width: 576px){.now_section .reads p{font-size:11px}}.now_section a{color:#FF6060}.now_section a:hover{border-radius:4px;border-bottom:4px solid #FF6060}.now_section:last-of-type{font-size:14px;margin-top:40px}.reads_book{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:flex-start;align-content:stretch;align-items:flex-start;padding:1rem 0;border-top:1px solid #559AFF;font-size:0.8rem}.reads_cover{flex:0.3 1;align-self:auto}@media (min-width: 576px){.reads_cover{flex:0.2 1}}.reads_cover img{display:inline;margin:0;max-width:80px}.reads_metadata{flex:0.7 1;align-self:auto}@media (min-width: 576px){.reads_metadata{flex:0.8 1}}.reads_metadata h3{margin-top:0}.reads_metadata h3 a{color:#FF6060}.reads_metadata p{margin:0.5rem 0}.light-mode{background-color:#FFFCF0;color:#02241F;-webkit-transition:background-color 0.25s ease-out;transition:background-color 0.25s ease-out}.light-mode nav{background-color:#FFFCF0;color:#02241F;-webkit-transition:background-color 0.25s ease-out;transition:background-color 0.25s ease-out}.dark-mode{background-color:#1a1919;color:#f1f1f1}.dark-mode nav{background-color:#1b2938}.dark-mode nav .navbar_left h1 a{color:#f1f1f1}.dark-mode nav .navbar_right a{color:#f1f1f1}.toggle{position:relative;padding:0 4px;display:flex;align-items:center}input[type="checkbox"].checkbox{width:40px;height:10px;background:#555;position:relative;border-radius:5px;-webkit-appearance:none;-moz-appearance:none;appearance:none;cursor:pointer;vertical-align:2px;outline:none}input[type="checkbox"].checkbox:checked+label{left:30px}input[type="checkbox"].checkbox:focus-visible{outline:solid 2px white}input[type="checkbox"].checkbox+label{display:inline-block;width:18px;height:18px;border-radius:50%;-webkit-transition:all 0.3s ease;transition:all 0.3s ease;cursor:pointer;position:absolute;left:2px;background:#fff;opacity:0.9;background-color:cyan}.toggle-container{display:flex;margin:0 auto}.toggle-container>span{font-size:1.2em;background:none;border:none;cursor:pointer;-webkit-transition:color 0.3s ease;transition:color 0.3s ease;margin-top:4px;margin-right:2px}.toggle-container>span:last-child{margin-top:5px}

